/*

MOBILE PROBLEMS:
    - Minor scroll on nav bar, even though it's set to fixed position
    - Existince of nav dropdown menu makes nav bar scrolling problem worse, even when display is set to none

*/

* {
    margin: 0;
    padding: 0;
    overflow-x: hidden;
}

.main {
    position: absolute;
    background: transparent;
    height: 100vh;
    width: 100vw;
}

.container {
    z-index: 100000;
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100vw;
    height: 8vh;
    background-color: rgba(0,0,0,0.65);
    overflow: hidden;
}

.nav {
    margin-top: 3px;
    overflow: hidden;
    height: 50%;
}

.nav button {
    color: rgba(50, 236, 191, 1);
    text-shadow: 2px 2px rgba(183, 35, 35, 1);
    text-decoration: none;
    display: block;
    background: transparent;
    border: none;
    font-size: 1.1em;
    margin: 50px;
}

.nav button:hover {
    color: rgb(93, 250, 90);
    cursor: pointer;
}

.nav li {
    list-style-type: none;
    float: left;
    margin-bottom: 6px;
}

.navLeft {
    display: flex;
    align-items: center;
    height: 100%;
    padding-left: 40px;
    margin-left: 10px;
}

.navRight {
    display: flex;
    align-items: center;
    height: 100%;
    padding-right: 40px;
    margin-right: 10px;
}

.navLeft a {
    margin-left: 30px;
}

.navRight a {
    margin-right: 30px;
}

.logo {
    z-index: 100000;
    position: absolute;
    width: auto;
    transition: all .5s;
}

.logoHero {
    height: 20vw;
    transform: translate(45vw, 15vw) rotate(-10deg);
    filter: drop-shadow(7px 7px rgba(183, 35, 35, 1));
}

.logoNav {
    position: fixed;
    width: 12vw;
    height: auto;
    transform: translate(41vw, 1.5vh) rotate(-10deg);
    filter: drop-shadow(3px 4px rgba(183, 35, 35, 1));
}

.menuButton {
    z-index: 1000000;
    display: none;
}

.navDrop {
    z-index: 1000000000;
    position: fixed;
    margin-top: 54px;
}

.navDrop ul {
    display: flex;
    flex-direction: column;
    margin-top: 3.5vh;
}

.navDrop button {
    width: 100vw;
    margin-top: 10px;
    font-size: 1.8em;
    color: rgba(50, 236, 191, 1);
    text-shadow: 2px 2px rgba(183, 35, 35, 1);
    background-color: rgba(0,0,0,0.65);
    border: none;
    padding-top: 5px;
    padding-bottom: 5px;
}

.navDrop button:hover {
    cursor: pointer;
    color: rgb(93, 250, 90)
}

/********************** RESPONSIVE BREAKPOINTS ********************************/

@media all and (orientation: landscape) {
    .nav {
        display: flex;
        justify-content: space-between;
        height: 100%;
        width: 30%;
        border: 1px solid red;
    }

    .nav ul {
        display: flex;
        justify-content: space-around;
        height: 100%;
        width: 100%;
        margin: 0;
        padding: 0;
    }

    .nav button {
        margin: 0px;
        height: 80%;
        padding-top: 10%;
        overflow: hidden;
    }

    .navRight {
        padding: 0;
        margin: 0;     
    }

    .navLeft {
        display: flex;
        justify-content: space-around;
        padding: 0;
        margin: 0;
    }
}

@media all and (orientation: portrait) {
    .menuButton {
        position: fixed;
        display: block;
        height: 4%;
        top: 2vh;
        right: 5vw;
    }

    .nav {
        margin-top: 3px;
    }
    
    .nav button {
        display: none;
    }
    
    .logoHero {
        width: 60vw;
        height: auto;
        transform: translate(20vw, 15vh) rotate(-10deg);
    }
    
    .logoNav {
        position: fixed;
        width: 25vw;
        height: auto;
        transform: translate(5vw, 2vh) rotate(-10deg);
        filter: drop-shadow(3px 4px rgba(183, 35, 35, 1));
    }
    
}




